}
blockquote .has-cite {
	margin-top: -1rem;
	text-align: center;
}

cite {
	font-size: 0.875em;
	font-style: normal;
	font-variant: small-caps;
}

q {
	quotes: "“" "”" "‘" "’";
}

table {
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
	margin-bottom: 2.125rem;
}

caption {
	margin: 0 0 5px;
	font-variant: small-caps;
}

th,
td {
	padding: 10px;
	box-sizing: border-box;
	vertical-align: top;
	text-align: left;
}
th:empty, th.pad,
td:empty,
td.pad {
	opacity: 0;
}

th {
	font-family: var(--font-family-headings);
	font-size: 16px;
	font-weight: 700;
	text-align: left;
}

td {
	border: 1px solid rgb( var( --color-border ) );
}

.app {
