.gallery {
	max-width: 100%;
	display: block;
	clear: both;
	margin: 1.414em 0 1rem;
}
.gallery::after {
	content: ".";
	display: block;
	height: 0;
	clear: both;
	visibility: hidden;
}
.gallery-item {
	width: 100%;
	margin: 0;
	padding: 0 0 1rem;
