.mejs-container {
	margin-bottom: 2.125rem;
}

body.menu-open {
	overflow-y: scroll;
	width: 100%;
}

.below-app-header {
	position: relative;
	width: 100%;
	margin: 0 auto;
	overflow-y: hidden;
